<template>
	<div>
		<div class="page-header clear-filter" filter-color="orange">
		<parallax
			class="page-header-image"
			style="background-image:url('https://ras-upload.s3.amazonaws.com/ckdb/img/background/bg-profile.jpeg')"
		>
		</parallax>
		<div class="container">
			<div class="photo-container" style="background-color: #FFF">
			<img :src="driver.image" alt="" />
			</div>
			<h3 class="title">{{driver.nickname}}</h3>
			<p class="category">{{driver.name}}</p>
			<div class="content">
			<div class="social-description">
				<h2>{{parseInt(driver.allScore)}}</h2>
				<p>Pontos na Carreira</p>
			</div>
			<div class="social-description">
				<h2>{{driver.podium}}</h2>
				<p>Podiuns na Carreira</p>
			</div>
			<div class="social-description">
				<h2>{{driver.poleposition}}</h2>
				<p>Total de <br/>Pole Posistions</p>
			</div>
			<div class="social-description">
				<h2>{{driver.bestlap}}</h2>
				<p>Total de <br/>Melhores Voltas</p>
			</div>
			</div>
		</div>
		</div>
		<div class="section">
			<div class="container">
				<div class="button-container">
					<a href="#button" class="btn btn-primary btn-round btn-lg">Editar Perfil</a>
				</div>
				<p class="category">Dados Cadastrais</p>
				<table class="vue-table" style="max-width: 400px; margin: 0 auto">
					<tr>
						<th>Telefone</th>
						<td class="kart">{{driver.phone}}</td>
					</tr>
					<tr class="race-table">
						<th>Email</th>
						<td class="kart">{{driver.email}}</td>
					</tr>
				</table>
				<div class="col-md-10 ml-auto col-xl-6 mr-auto">
					<h3 class="title">Campeonato Atual</h3>
					<!-- Tabs with Background on Card -->
					<div class="card">
						<tabs
						centered
						type="neutral"
						tab-nav-wrapper-classes="card-header"
						tab-content-classes="card-body text-center"
						data-background-color="orange"
						>
							<tab-pane>
								<template slot="label">
								<i class="now-ui-icons sport_trophy"></i> Pontuação
								</template>
								<div class="row">
									<div>
										<h4>Pontuação Atual</h4>
										<p>
										{{parseInt(driver.currentScore)}}
										</p>
									</div>
									<div>
										<h4>Baterias Corridas</h4>
										<p>
										{{results.length}}
										</p>
									</div>
								</div>
							</tab-pane>
							<tab-pane v-show="driver.team_id">
								<template slot="label">
								<i class="now-ui-icons objects_spaceship"></i> Equipe
								</template>
								<div class="row">
									<div>
										<h4>Equipe Atual</h4>
										<p>
										{{driver.team_id ? driver.team_id.name : "Independente"}}
										</p>
									</div>
									<div>
										<h4>Pontuação da Equipe</h4>
										<p>
										{{driver.team_id ? parseInt(driver.team_id.currentScore) : 0}}
										</p>
									</div>
								</div>
							</tab-pane>
						</tabs>
					</div>
					<!-- End Tabs on plain Card -->
				</div>
				<h3 class="title">Depoimento</h3>
				<h5 class="description">
				{{driver.deposition}}
				</h5>
				<div class="col-md-6 ml-auto mr-auto">
					<h4 class="title text-center">Minha Corridas</h4>
				</div>
				<results-table :results="results"></results-table>
			</div>
		</div>
	</div>
</template>
<script>
import { ResultsTable, Tabs, TabPane } from '@/components';
import axios from '../services/api'

export default {
	name: 'profile',
	bodyClass: 'profile-page',
	components: {
		ResultsTable,
		Tabs,
    	TabPane
	},
	data() {
		return {
			driver: {},
			alert: {
				type: "success",
				visible: false,
				message: ""
			},
			results: []
		}
	},
	created() {
		this.getDriver()
	},
	methods: {
		async getDriver() {
			await axios.get('/profile')
			.then(response => {
				this.driver = response.data
			}).catch(error => {
				this.alert.type = error.response.status === 400 ? "warning" : "danger"
				this.alert.message = error.response.data.error.userMessage
				this.alert.visible = true
			})

			await axios.get("/drivers/races", {
				params: {
					"driverName": this.driver.name
				}
			}).then(response => {
				this.results = response.data
			}).catch(error => {
				this.alert.type = error.response.status === 400 ? "warning" : "danger"
				this.alert.message = error.response.data.error.userMessage
				this.alert.visible = true
			})
		}
	}
};
</script>
