r a 14 anos, desde que esteja em plena condição física e mental para a prática do esporte. ")]),_c('h5',[_vm._v(" 1.4. Todos os participantes deverão assinar um termo de responsabilidade, declarando estar em plenas condições físicas e mentais para a prática do kartismo. Os pilotos assumem toda e qualquer responsabilidade por acidentes e danos físicos durante todo o decorrer do evento. No caso de menores de 18 anos, os pais ou responsáveis (devidamente identificados) deverão assinar uma autorização, nos mesmos moldes, permitindo a participação do menor no evento. ")]),_c('h5',[_vm._v(" 1.5. Fica reservada à comissão organizadora do evento a aprovação, ou não, dos pilotos que se inscreverem, sem que haja necessidade de justificativa. ")]),_c('h5',[_vm._v(" 1.6. As regras para inscrição, pagamento, valores e notícias importantes sobre o evento deverão ser acompanhadas sempre no site oficial do KART DO BARBEIRO, (https://kartdobarbeiro.herokuapp.com/) e mídias sociais. Considera-se, ainda, que os pilotos que não cumprirem os prazos de pagamento perderão o direito de participação. ")]),_c('h4',[_vm._v("2. CATEGORIAS")]),_c('h5',[_vm._v(" 2.1. A competição terá 2 categorias gerais, com participação livre de qualquer piloto, de qualquer peso e de ambos os sexos, com lastreamento de todos os pilotos em 90 kg (considerando equipamento). ")]),_c('h4',[_vm._v("3. CALENDÁRIO - CATEGORIA F1/F2")]),_c('h5',[_vm._v(" 3.1. Na primeira edição do ano de 2021, campeonato será realizada com quatro etapas conforme o calendário a seguir: "),_c('h6',[_vm._v(" 3.1.a. 1ª Etapa (29/01/2022) - Traçado James Hunt - Kartódromo dos Ingleses; ")]),_c('h6',[_vm._v(" 3.1.b. 2ª Etapa (19/02/2022) - Traçado Hamilton - Kartódromo dos Ingleses; ")]),_c('h6',[_vm._v(" 3.1.c. 3ª Etapa (27/03/2022) - Traçado Ayrton Senna - Kartódromo dos Ingleses; ")]),_c('h6',[_vm._v(" 3.1.d. 4ª Etapa (23/04/2022) - Traçado Tradicional - Kartódromo do Beto Carrero World ")]),_c('h6',[_vm._v(" 3.1.e. 5ª Etapa (22/05/2022) - Traçado Verstappen - Kartódromo dos Ingleses; ")]),_c('h6',[_vm._v(" 3.1.f. 6ª Etapa (25/06/2022) - Traçado Tradicional - Kartódromo dos Ingleses; ")]),_c('h6',[_vm._v(" 3.1.g. As datas podem sofrer alteração por parte do kartódromo, no qual vamos reagendar a etapa com antecedência. ")])]),_c('h4',[_vm._v("4. FORMA DE DISPUTA")]),_c('h5',[_vm._v(" 4.1. Cada piloto, poderá disputar até 6 corridas com duas baterias de 15 minutos, com tomada de tempo no seguinte sistema: "),_c('h6',[_vm._v(" 4.1.a. BATERIA 1 – Tomada de tempo de 5 minutos. ")]),_c('h6',[_vm._v(" 4.1.b. BATERIA 2 – O grid será formado através da inversão da posição de chegada da corrida 1. Ex.: 1º colocado larga em último e o último em primeiro, e assim sucessivamente. ")]),_c('h6',[_vm._v(" 4.1.c. Todas formadas por um número de participantes que se adeque com o número de inscrições e disponibilidade de karts por parte do kartódromo. ")])]),_c('h5',[_vm._v(" 4.2. Todos os pilotos disputarão provas em todos os dias de evento de acordo com o cronograma divulgado no site oficial do evento. ")]),_c('h5',[_vm._v(" 4.3. Cada vez que um piloto for para a pista disputar uma bateria, participará de uma “série”. Sendo assim, o total será de duas séries de baterias em cada uma das corridas. ")]),_c('h5',[_vm._v(" 4.4. Em cada série disputada, o piloto irá pontuar de acordo com a tabela do item 5.2. ")]),_c('h5',[_vm._v(" 4.5. Será utilizado um descarte, sendo ele o pior resultado em uma Corrida. Dessa forma, serão considerados 5 resultados obtidos nas corridas para calcular e apontar os pilotos. ")]),_c('h4',[_vm._v("5. FORMAÇÃO DE BATERIAS")]),_c('h5',[_vm._v(" 5.1. Entende-se por “formação de baterias” a distribuição dos pilotos dentre as baterias de uma categoria, em casos de categorias com mais de uma bateria de pilotos. ")]),_c('h5',[_vm._v(" 5.2. A formação das baterias de cada categoria se dará da seguinte forma: "),_c('h6',[_vm._v(" 5.2.a. CLASSIFICATÓRIA 1: Os pilotos serão divididos por grupo, através de sorteio, de acordo com a quantidade de inscritos por categoria. O sorteio se dará em dia e horário a ser divulgado. ")]),_c('h6',[_vm._v(" 5.2.b. CLASSIFICATÓRIAS 2: Serão formadas a partir da inversão da posição de chegada da Bateria 1, respectivamente, de acordo com o exemplo abaixo: Ex.: O 1º colocado da Bateria 1 largará em último na Bateria 2, e o último colocado da Bateria 1, largará em primeiro na Bateria 2. ")])]),_c('h4',[_vm._v("6. PONTUAÇÃO")]),_c('h5',[_vm._v(" 6.1. Entende-se por “Pontuação” a aplicação de pontos, de acordo com o resultado final de cada piloto e/ou bônus recebidos. ")]),_c('h5',[_vm._v(" 6.2. A pontuação de cada piloto, obtida nas baterias classificatórias, seguirá a seguinte tabela de classificação: 1º 25 2º 22 3º 21 4º 19 5º 17 6º 15 7º 14 8º 13 9º 12 10º 11 11º 10 12º 9 13º 8 14º 7 15º 6 16º 5 17º 4 18º 3 19º 2 20º 1 ")]),_c('h5',[_vm._v(" 6.3. Além da pontuação da tabela acima, haverá também a bonificação de 1 ponto pela pole position (conquistada no tempo de classificação, na tomada de tempo) e mais 1 ponto pela volta mais rápida da corrida. "),_c('span',[_vm._v("CONSIDERA-SE POLE E VOLTA MAIS RÁPIDA PARA AQUELE QUE A FIZER PRIMEIRO.")])]),_c('h5',[_vm._v(" 6.4. Todos os pilotos que largarem receberão a pontuação referente a sua posição de término, desde que completem ao menos 75% do número de voltas do vencedor da corrida. ")]),_c('h5',[_vm._v(" 6.5. Os seguintes critérios, na ordem apresentada, serão levados em conta para eventual desempate na classificação geral: "),_c('h6',[_vm._v(" 6.5.a. Maior número de vitórias; ")]),_c('h6',[_vm._v(" 6.5.b. Persistindo o empate, maior número de segundos lugares, de terceiros lugares, de quartos lugares, e assim sucessivamente, até o desempate; ")]),_c('h6',[_vm._v(" 6.5.c. Maior número de melhor volta; ")]),_c('h6',[_vm._v(" 6.5.d. Maior número de pole positions. ")]),_c('h6',[_vm._v(" 6.5.e. Sorteio ")])]),_c('h5',[_vm._v(" 6.6. O critério de desempate para definir os vencedores de cada etapa será: "),_c('h6',[_vm._v(" 6.6.a. Maior número de vitórias; ")]),_c('h6',[_vm._v(" 6.6.b. Persistindo o empate, maior número de segundos lugares, de terceiros lugares, de quartos lugares, e assim sucessivamente, até o desempate; ")]),_c('h6',[_vm._v(" 6.6.c. Melhor colocação na primeira corrida. ")])]),_c('h4',[_vm._v("7. USO E SORTEIO DOS KARTS")]),_c('h5',[_vm._v(" 7.1. Serão utilizados os karts de aluguel disponibilizados pelo kartódromo que sediará a corrida, sendo de total responsabilidade do kartódromo a manutenção, equalização e condições gerais de seus equipamentos. ")]),_c('h5',[_vm._v(" 7.2. Em todas as baterias e em todas as séries, os karts serão sorteados pela organização do evento através do método que julgar adequado. O sorteio será feito antes do início das provas e na presença dos pilotos que farão parte da bateria. ")]),_c('h5',[_vm._v(" 7.3. Não será permitida a troca de kart VOLUNTÁRIA antes da largada. ")]),_c('h5',[_vm._v(" 7.4. Durante as classificatórias, caso ocorra uma quebra do kart (devidamente comprovada por mecânicos do kartódromo) ainda no parque fechado ou no grid de largada, antes da largada, o piloto será autorizado a trocar o kart, mediante sorteio, podendo alinhar-se em sua posição original de largada. ")]),_c('h5',[_vm._v(" 7.5. Caso ocorra alguma quebra durante a corrida, o piloto deverá aguardar no local até que um kart substituto seja levado pelo staff do kartódromo. ")]),_c('h5',[_vm._v(" 7.6. Não é permitido ao piloto, em hipótese alguma: mexer, preparar, calibrar ou efetuar qualquer tipo de regulagem/alteração no kart que lhe foi sorteado, tanto com o kart parado quanto em movimento. A única regulagem autorizada é a de posição de banco. A calibragem de pneus só poderá ser efetuada mediante constatação de um mecânico de que realmente existe essa necessidade. Mesmo assim, o procedimento só poderá ser efetuado por um dos mecânicos do kartódromo. ")]),_c('h5',[_vm._v(" 7.7. Não será permitido o uso de rádios comunicadores para orientar os pilotos dentro da pista ou para qualquer fim dentro do kartódromo. Tais equipamentos deverão ser utilizados, exclusivamente, pelas pessoas envolvidas com a organização do evento ou funcionários do kartódromo, com a única finalidade de se comunicarem entre si. ")]),_c('h5',[_vm._v(" 7.8. Placas, lousas e gestos com as mãos serão aceitos, desde que os responsáveis por tais formas de comunicação com os pilotos se mantenham no local destinado ao público. É proibida a entrada de pessoas não autorizadas na pista ou em áreas restritas. ")]),_c('h4',[_vm._v("8. USO DE LASTROS E PESO MÍNIMO")]),_c('h5',[_vm._v(" 8.1. Todos os pilotos deverão ir para a pista com um peso mínimo de 90 kg. Em todos os casos considerando equipamento. ")]),_c('h5',[_vm._v(" 8.2. A capacidade máxima de lastro permitida pelo kartódromo, em cada kart, é de 20kg (ou quatro barras). Portanto, os pilotos que não atingirem o peso mínimo obrigatório deverão providenciar lastro por conta própria. ")]),_c('h5',[_vm._v(" 8.3. Os pesos só poderão ser carregados por dentro do macacão e devidamente presos junto ao corpo do piloto. Também serão aceitos lastros em forma de mantas que se moldam ao banco. Mesmo assim, ainda haverá uma avaliação da organização do evento e da direção do kartódromo, para verificar se o tipo de peso portado no corpo do piloto não coloca em risco a sua segurança e integridade física. Não serão permitidos pesos nos bolsos, soltos no kart, amarrados na barra de direção ou soltos nos bancos. ")]),_c('h5',[_vm._v(" 8.4. O piloto é totalmente responsável por colocar o lastro em seu kart. Caso isso não ocorra e o piloto for para a pista abaixo do peso limite, será desclassificado da prova. ")]),_c('h5',[_vm._v(" 8.5. Todos os pilotos serão pesados antes de suas baterias. Ao final de cada bateria os 7 primeiros colocados serão pesados , estes deverão aguardar em seus karts serem chamados para a balança , não podendo os mesmos se deslocarem para qualquer fim sob pena de desclassificação da prova , caso algum piloto apresente peso inferior a 500g do peso estabelecido por este regulamento, será desclassificado da bateria em que acabou de participar, recebendo pontuação zero e a mesma não poderá ser descartada. ")]),_c('h5',[_vm._v(" 8.6. Caso algum piloto necessite do uso de pedaleiras ou travesseiros e almofadas para alcançar os pedais, visando seu maior conforto e segurança, poderá utilizá-los. O uso será liberado desde que não altere as configurações originais do kart e nem ocasione atrasos (tanto na entrada quanto na saída de sua bateria, visando o bom andamento do evento). ")]),_c('h4',[_vm._v("9. EQUIPAMENTO DE SEGURANÇA")]),_c('h5',[_vm._v(" 9.1. Os pilotos deverão obrigatoriamente utilizar os seguintes equipamentos: "),_c('h6',[_vm._v(" 9.1.a. Capacete integral, com viseira. ")]),_c('h6',[_vm._v(" 9.1.b. Luvas que cobrem completamente os dedos, palma e dorso das mãos. ")]),_c('h6',[_vm._v(" 9.1.c. Sapatilha própria para a prática do kartismo ou automobilismo. Na falta desta, será permitido o uso de tênis ou calçado equivalente, do tipo fechado. Não será permitido uso de calçados abertos, tipo chinelo, sandálias ou assemelhados. ")]),_c('h6',[_vm._v(" 9.1.d. Macacão próprio para a prática do kartismo ou automobilismo. ")]),_c('h6',[_vm._v(" 9.1.e. No caso de piloto com cabelos longos, é obrigatório também o uso de balaclava. ")])]),_c('h5',[_vm._v(" 9.2. O kartódromo disponibilizará aos participantes um conjunto de capacete, luvas, balaclava e macacão (caso não disponibilize o item não será obrigatório). ")]),_c('h5',[_vm._v(" 9.3. É recomendada a utilização dos seguintes equipamentos de segurança: "),_c('h6',[_vm._v(" 9.3.a. Colete protetor de costelas ")]),_c('h6',[_vm._v(" 9.3.b. Colar de proteção cervical (pescoceira). ")]),_c('h6',[_vm._v(" 9.3.c. Balaclava ou capuz de proteção. ")]),_c('h6',[_vm._v(" 9.3.d. Qualquer outro equipamento de segurança que o piloto julgue conveniente, tais como joelheiras, cotoveleiras, espumas de proteção, etc. ")])]),_c('h5',[_vm._v(" 9.4. Não é de responsabilidade da organização, ou de seus representantes fornecer qualquer equipamento de segurança obrigatório. ")]),_c('h5',[_vm._v(" 9.5. À organização, se reserva o direito de IMPEDIR de participar da prova o piloto que não se apresentar com o equipamento mínimo de segurança descrito neste regulamento. ")]),_c('h4',[_vm._v("10. PISTA, TRAÇADOS E TREINOS")]),_c('h5',[_vm._v(" 10.1. O KARTÓDROMO INTERNACIONAL DOS INGLESES disponibiliza diversas opções de traçados que poderão ser utilizados para o evento. No entanto, os traçados oficiais só serão divulgados na semana do evento, podendo haver alteração por parte da organização (a qualquer momento), a depender de questões climáticas e/ou segurança, bem como de necessidades do próprio kartódromo. ")]),_c('h5',[_vm._v(" 10.2. Os treinos livres poderão ser marcados por qualquer piloto ou clube, diretamente com o kartódromo, após a divulgação dos traçados oficiais. ")]),_c('h4',[_vm._v("11. ADVERTÊNCIAS E PUNIÇÕES")]),_c('h5',[_vm._v(" 11.1. A organização do evento não responde por decisões que os Diretores Desportivos ou responsáveis pela fiscalização de pista venham a tomar. A direção de provas é de total responsabilidade do kartódromo que sediar a corrida e dos terceirizados contratados para tal. ")]),_c('h5',[_vm._v(" 11.2. As ocorrências de pista poderão implicar em: • ADVERTÊNCIA (ADV) - BANDEIRA PRETA E BRANCA. Implica em 5 segundos a serem acrescentados ao tempo da bateria. • PUNIÇÃO (PUN) - BANDEIRA PRETA E LARANJA. Implica em 15 segundos a serem acrescentados ao tempo da bateria. Esta bandeira não necessariamente deverá ser antecedida de uma preta e branca, podendo assim ser aplicada diretamente. • DESCLASSIFICAÇÃO (DESC) - BANDEIRA PRETA. O piloto que receber esta bandeira será automaticamente desclassificado da bateria e terá 3 voltas para recolher o kart para o parque fechado. O piloto que não obedecer a bandeira preta ou, depois desta, praticar conduta antidesportiva na pista, será automaticamente excluído do evento ")]),_c('h5',[_vm._v(" 11.3. PUNIÇÕES ADMINISTRATIVAS - Haverá punição administrativa para as seguintes infrações: "),_c('h6',[_vm._v(" 11.3.a. Acesso do piloto ao parque fechado sem autorização: - Perda de 3 pontos na competição ")]),_c('h6',[_vm._v(" 11.3.b. Manobra perigosa dentro do parque fechado: - Perda de 3 pontos na competição ")]),_c('h6',[_vm._v(" 11.3.c. Chegar atrasado ao briefing: - Perda de 3 pontos na competição ")]),_c('h6',[_vm._v(" 11.3.d. Sair do parque fechado sem autorização: - Perda de 3 pontos na competição ")]),_c('h6',[_vm._v(" 11.3.e. Não utilizar máscara de proteção facial: - Perda de 3 pontos na competição ")]),_c('h6',[_vm._v(" 11.3.f. Correr abaixo do peso mínimo estabelecido: - Desclassificação da corrida ")]),_c('h6',[_vm._v(" 11.3.g. Fazer ajuste não permitido no kart: - Desclassificação da corrida ")]),_c('h6',[_vm._v(" 11.3.h. Usar um kart diferente do sorteado: - Desclassificação da corrida ")])]),_c('h5',[_vm._v(" 11.4. O tempo de penalização, referentes ao item 10.2, será aplicado diretamente ao tempo total do piloto na bateria e a posição será calculada após o acréscimo do tempo. ")]),_c('h5',[_vm._v(" 11.5. Os pontos de penalização, referentes ao item 10.3, serão aplicados diretamente à pontuação geral do piloto, ou seja, não serão aplicados diretamente à pontuação da corrida. Portanto, pontos de penalização não são considerados para o descarte do pior resultado e já serão computados desde o início da competição. ")]),_c('h5',[_vm._v(" 11.6. O piloto que desrespeitar seu companheiro, funcionários, membros da organização ou qualquer outra pessoa, terá sua ação avaliada pela organização do evento e poderá sofrer sanções, que variam desde uma advertência até a expulsão do evento. ")]),_c('h5',[_vm._v(" 11.7. Todo piloto é responsável pelo comportamento de sua torcida durante a realização do evento, podendo este ser punido, a critério da organização. ")]),_c('h5',[_vm._v(" 11.8. A organização do evento solicita que nenhum piloto consuma bebida alcoólica durante a realização do evento. Caso algum piloto se apresente para pesagem e sorteio dos karts em condições duvidosas, poderá ser impedido pela organização do evento de participar de sua prova. ")]),_c('h5',[_vm._v(" 11.9. Caso ao final da competição algum piloto seja desclassificado, por qualquer motivo que seja, os demais competidores abaixo dele subirão uma posição na tabela. 11.10. Os membros da Comissão Organizadora que estiverem competindo em alguma bateria são considerados “apenas” pilotos e, portanto, estão susceptíveis às mesmas regras e punições da competição. 11.11. A organização é soberana para decidir a penalização que será aplicada em casos não previstos neste regulamento, podendo consultar o Diretor de Provas, Comissários de Pista, Fiscais de Pista e até mesmo os pilotos para chegar a uma conclusão. ")]),_c('h4',[_vm._v("12. PREMIAÇÃO")]),_c('h5',[_vm._v(" 12.1. Os prêmios relatados nesta seção têm suas cores, materiais, tamanhos e formas definidos a livre critério da organização do evento e seus patrocinadores. ")]),_c('h5',[_vm._v(" 12.2. Ao decorrer da competição as premiações serão as seguintes: "),_c('h6',[_vm._v(" 12.2.a. Os 5 primeiros colocados de cada corrida irão ao pódio e receberão troféus exclusivos. NESTE MOMENTO, DEVERÃO ESTAR COM SUA VESTIMENTA COMPLETA (MACACÃO FECHADO OU CAMISETA DA EQUIPE). ")])]),_c('h5',[_vm._v(" 12.3. Ao final da competição, as premiações serão as seguintes: "),_c('h6',[_vm._v(" 12.3.a. Os 5 primeiros colocados de cada categoria irão ao pódio e receberão troféus exclusivos. NESTE MOMENTO, DEVERÃO ESTAR COM SUA VESTIMENTA COMPLETA (MACACÃO FECHADO OU CAMISETA DA EQUIPE). ")]),_c('h6',[_vm._v(" 12.3.b. Além das disputas individuais, haverá também a disputa por EQUIPES. Para tal, os pilotos deverão informar, no ato da inscrição, a qual equipe pertencem. ")]),_c('h6',[_vm._v(" 12.3.c. A disputa por EQUIPES se dará durante 