te de acordo e ciente que deverá pagar uma taxa de R$100,00. Esta será devidamente descontada da restituição a ser feita por parte da organização do evento, caso desista de participar do evento em questão, seja por qualquer motivo. Caso o cancelamento seja realizado dentro do prazo legal previsto no art. 49 da Lei nº 8078/90, ou seja, em até 7 dias da data da inscrição, a restituição dos valores pagos será integral, nos moldes do parágrafo único do mesmo artigo de lei. ")]),_c('h5',[_vm._v(" 14.2. Considerando que o piloto teria um grande prejuízo caso OS ORGANIZADORES desistissem de fazer o evento, entendemos que os ORGANIZADORES também têm prejuízos quando um piloto desiste de participar do mesmo evento. ")]),_c('h5',[_vm._v(" 14.3. A TAXA em questão, que é pequena perto dos custos e despesas causadas pelas desistências, será utilizada para pagar o trabalho administrativo e todas as taxas cobradas pelos bancos, além de outras despesas relacionadas à desistência. ")]),_c('h5',[_vm._v(" 14.4. Outrossim, considerando que os fornecedores são pagos com antecedência de até 72 horas ou mais da data do evento, não serão realizadas devoluções dos valores pagos para desistências realizadas fora do prazo legal acima mencionado. ")]),_c('h4',[_vm._v("15. DISPOSIÇÕES FINAIS")]),_c('h5',[_vm._v(" 15.1. Todo piloto inscrito atesta que está ciente de toda a documentação aqui contida e aceita as normas de funcionamento da organização desta competição. ")]),_c('h5',[_vm._v(" 15.2. Todo piloto inscrito autoriza o uso de sua imagem em áudio, vídeo, fotos e todo e qualquer tipo de material veiculado pela organização do evento, seja para divulgação na imprensa, em seu site ou para uso comercial na venda de material do evento. ")]),_c('h5',[_vm._v(" 15.3. Este regulamento só poderá ser alterado pela comissão organizadora e apenas em casos extremos. Fica, portanto, a organização autorizada a publicar adendos ou efetuar alterações, visando a melhorias ou correções. Caso isto ocorra, todos